import CardLayout from '~/components/CardLayout.vue';
import { mapActions } from 'vuex';

export default {
	components: {
		CardLayout
	},
	data () {
		return {
			email: '',
			password: '',
			loading: false,
			error: ''
		};
	},
	computed: {
		isError () {
			return !!this.error;
		}
	},
	methods: {
		async handleSubmit () {
			try {
				this.loading = true;
				await this.loginUser({ identifier: this.email, password: this.password });
				this.loading = false;
				this.$router.push('/');
			} catch (err) {
				this.loading = false;
				this.error = err.message || 'Incorrect email or password';
			}
		},
		...mapActions({
			loginUser: 'auth/loginUser'
		})
	}
};
